/**
 * 
 * GLMAPI
 * AI
 */

import zhipuTranslationService from './zhipuTranslationService.js';
import { logger } from '../config/debug.js';

//  - GLMAPI
export const TRANSLATION_ENGINES = {
  zhipu: {
    name: 'GLM',
    provider: 'zhipu-ai',
    description: 'AI GLM-4-FlashAI',
    url: 'https://bigmodel.cn/',
    priority: 1,
    available: true,
    languages: 100,
    features: ['AI', '', '', ''],
    implementation: 'ai_model',
    type: 'primary'
  },
  google: {
    name: 'Google',
    provider: 'google',
    description: 'Google134',
    url: 'https://translate.google.com',
    priority: 2,
    available: true,
    languages: 134,
    features: ['', ''],
    implementation: 'web_api',
    type: 'fallback'
  },
  alibaba: {
    name: '',
    provider: 'alibaba',
    description: '221',
    url: 'https://translate.alibaba.com',
    priority: 3,
    available: true,
    languages: 221,
    features: ['', ''],
    implementation: 'web_scraping',
    type: 'fallback'
  },
  baidu: {
    name: '',
    provider: 'baidu',
    description: '201',
    url: 'https://fanyi.baidu.com',
    priority: 4,
    available: true,
    languages: 201,
    features: ['', ''],
    implementation: 'web_scraping',
    type: 'fallback'
  }
};

/**
 * 
 */
const LANGUAGE_MAPPING = {
  'zh': { name: '', google: 'zh', alibaba: 'zh', baidu: 'zh' },
  'en': { name: '', google: 'en', alibaba: 'en', baidu: 'en' },
  'ja': { name: '', google: 'ja', alibaba: 'ja', baidu: 'jp' },
  'ko': { name: '', google: 'ko', alibaba: 'ko', baidu: 'kor' },
  'fr': { name: '', google: 'fr', alibaba: 'fr', baidu: 'fra' },
  'de': { name: '', google: 'de', alibaba: 'de', baidu: 'de' },
  'es': { name: '', google: 'es', alibaba: 'es', baidu: 'spa' },
  'ru': { name: '', google: 'ru', alibaba: 'ru', baidu: 'ru' },
  'it': { name: '', google: 'it', alibaba: 'it', baidu: 'it' },
  'pt': { name: '', google: 'pt', alibaba: 'pt', baidu: 'pt' },
  'ar': { name: '', google: 'ar', alibaba: 'ar', baidu: 'ara' },
  'th': { name: '', google: 'th', alibaba: 'th', baidu: 'th' },
  'vi': { name: '', google: 'vi', alibaba: 'vi', baidu: 'vie' },
  'hi': { name: '', google: 'hi', alibaba: 'hi', baidu: 'hi' }
};

/**
 * 
 */
class NewTranslationManager {
  constructor() {
    this.engines = { ...TRANSLATION_ENGINES };
    this.currentEngine = 'zhipu'; // GLM
    this.zhipuService = zhipuTranslationService;
    this.translationCache = new Map();
    this.healthCheckInterval = null;
    this.lastHealthCheck = {};
    this.healthStatus = {};
    this.fallbackAttempts = 0;
    this.maxFallbackAttempts = 3;
    
    logger.translation(' [NewTranslationManager] ');
    logger.translation(' [NewTranslationManager] : GLM (AI)');
    
    this.initializeEngines();
  }

  /**
   * API
   * @param {string} apiKey - API
   */
  setZhipuApiKey(apiKey) {
    try {
      this.zhipuService.setApiKey(apiKey);
      logger.translation(' [NewTranslationManager] API');
      
      // 
      this.checkZhipuHealth().then(isHealthy => {
        this.healthStatus.zhipu = isHealthy;
        logger.translation(' [NewTranslationManager] :', isHealthy);
      });
      
      return true;
    } catch (error) {
      logger.error(' [NewTranslationManager] API:', error);
      return false;
    }
  }

  /**
   * API
   * @returns {boolean} API
   */
  hasZhipuApiKey() {
    return this.zhipuService.hasApiKey();
  }

  /**
   * GLM
   * @returns {Promise<boolean>} 
   */
  async checkZhipuHealth() {
    try {
      if (!this.zhipuService.hasApiKey()) {
        logger.warn(' [NewTranslationManager] GLMAPI');
        return false;
      }
      
      //  - 
      const testResult = await this.zhipuService.translate('test', 'zh', 'en');
      const isHealthy = testResult && testResult.status === 'success';
      
      logger.translation(`${isHealthy ? '' : ''} [NewTranslationManager] GLM: ${isHealthy ? '' : ''}`);
      return isHealthy;
    } catch (error) {
      logger.warn(' [NewTranslationManager] GLM:', error.message);
      return false;
    }
  }

  /**
   * 
   */
  async initializeEngines() {
    logger.translation(' [NewTranslationManager] ...');
    
    // GLM
    try {
      this.healthStatus.zhipu = await this.checkZhipuHealth();
    } catch (error) {
      logger.warn(' [NewTranslationManager] GLM:', error.message);
      this.healthStatus.zhipu = false;
    }
    
    // 
    Object.keys(this.engines).forEach(key => {
      if (key !== 'zhipu') {
        this.healthStatus[key] = true; // API
      }
    });
    
    logger.translation(' [NewTranslationManager] :', this.healthStatus);
  }



  /**
   * 
   */
  getCurrentEngine() {
    return {
      key: this.currentEngine,
      ...this.engines[this.currentEngine]
    };
  }

  /**
   * 
   */
  getAllEngines() {
    return Object.entries(this.engines).map(([key, engine]) => ({
      key,
      ...engine,
      lastCheck: this.lastHealthCheck[key]
    }));
  }

  /**
   * 
   */
  detectLanguage(text) {
    return this.zhipuService.detectLanguage(text);
  }

  /**
   * GLM
   */
  async translateWithZhipu(text, targetLang, sourceLang = 'auto') {
    try {
      logger.translation(` [NewTranslationManager] GLM`);
      const result = await this.zhipuService.translate(text, targetLang, sourceLang);
      
      // 
      this.fallbackAttempts = 0;
      
      return result;
    } catch (error) {
      logger.error(' [NewTranslationManager] GLM:', error.message);
      throw error;
    }
  }

  /**
   * API ()
   */
  async translateWithFallback(text, targetLang, sourceLang = 'auto', engineKey = 'google') {
    try {
      logger.translation(` [NewTranslationManager] : ${this.engines[engineKey]?.name}`);
      
      const detectedLang = sourceLang === 'auto' ? this.detectLanguage(text) : sourceLang;
      const sourceCode = LANGUAGE_MAPPING[detectedLang]?.[engineKey] || detectedLang;
      const targetCode = LANGUAGE_MAPPING[targetLang]?.[engineKey] || targetLang;
      
      // Google TranslateAPI
      const url = `https://translate.googleapis.com/translate_a/single?client=gtx&sl=${sourceCode}&tl=${targetCode}&dt=t&q=${encodeURIComponent(text)}`;
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36'
        }
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();
      
      if (data && data[0] && Array.isArray(data[0])) {
        const translatedText = data[0]
          .filter(item => item && item[0])
          .map(item => item[0])
          .join('');

        if (translatedText && translatedText.trim()) {
          return {
            translatedText: translatedText.trim(),
            engine: engineKey,
            confidence: 0.85,
            status: 'success',
            sourceLanguage: detectedLang,
            targetLanguage: targetLang,
            fromFallback: true
          };
        }
      }
      
      throw new Error('');
    } catch (error) {
      logger.error(` [NewTranslationManager] ${engineKey} :`, error.message);
      throw error;
    }
  }

  /**
   *  - 
   */
  async smartTranslate(text, targetLang = 'en', sourceLang = 'auto') {
    if (!text || !text.trim()) {
      throw new Error('');
    }

    // 
    const cacheKey = `${text}_${sourceLang}_${targetLang}`;
    if (this.translationCache.has(cacheKey)) {
      const cached = this.translationCache.get(cacheKey);
      logger.translation(' [NewTranslationManager] ');
      return { ...cached, fromCache: true };
    }

    let result = null;
    let lastError = null;

    // 1. GLM
    if (this.engines.zhipu.available && this.fallbackAttempts < this.maxFallbackAttempts) {
      try {
        result = await this.translateWithZhipu(text, targetLang, sourceLang);
        logger.translation(' [NewTranslationManager] GLM');
      } catch (error) {
        lastError = error;
        this.fallbackAttempts++;
        logger.warn(` [NewTranslationManager] GLM (${this.fallbackAttempts}/${this.maxFallbackAttempts}):`, error.message);
        
        // API Key
        if (error.message.includes('API Key') || error.message.includes('401') || error.message.includes('403')) {
          this.engines.zhipu.available = false;
          logger.warn(' [NewTranslationManager] GLM API Key');
        }
      }
    }

    // 2. GLM
    if (!result) {
      const fallbackEngines = ['google', 'alibaba', 'baidu'];
      
      for (const engineKey of fallbackEngines) {
        if (!this.engines[engineKey]?.available) continue;
        
        try {
          result = await this.translateWithFallback(text, targetLang, sourceLang, engineKey);
          logger.translation(` [NewTranslationManager]  ${this.engines[engineKey].name} `);
          break;
        } catch (error) {
          lastError = error;
          logger.warn(` [NewTranslationManager]  ${this.engines[engineKey].name} :`, error.message);
          this.engines[engineKey].available = false;
        }
      }
    }

    // 3. 
    if (!result) {
      throw new Error(`: ${lastError?.message || ''}`);
    }

    // 
    this.translationCache.set(cacheKey, result);
    if (this.translationCache.size > 500) {
      const firstKey = this.translationCache.keys().next().value;
      this.translationCache.delete(firstKey);
    }

    return result;
  }

  /**
   * 
   */
  async batchTranslate(texts, targetLang = 'en', sourceLang = 'auto') {
    if (!Array.isArray(texts) || texts.length === 0) {
      throw new Error('');
    }

    logger.translation(` [NewTranslationManager]  ${texts.length} `);

    // GLM
    if (this.engines.zhipu.available) {
      try {
        const results = await this.zhipuService.batchTranslate(texts, targetLang, sourceLang);
        logger.translation(' [NewTranslationManager] GLM');
        return results;
      } catch (error) {
        logger.warn(' [NewTranslationManager] GLM:', error.message);
      }
    }

    // 
    const results = [];
    for (let i = 0; i < texts.length; i++) {
      try {
        const result = await this.smartTranslate(texts[i], targetLang, sourceLang);
        results.push({
          index: i,
          original: texts[i],
          ...result
        });
        
        // 
        if (i < texts.length - 1) {
          await new Promise(resolve => setTimeout(resolve, 300));
        }
      } catch (error) {
        results.push({
          index: i,
          original: texts[i],
          error: error.message,
          status: 'failed'
        });
      }
    }

    logger.translation(` [NewTranslationManager] : ${results.filter(r => r.status === 'success').length}/${texts.length} `);
    return results;
  }

  /**
   * 
   */
  async checkEngineHealth(engineKey) {
    const engine = this.engines[engineKey];
    if (!engine) return false;

    const startTime = Date.now();
    try {
      logger.translation(` [NewTranslationManager]  ${engine.name} `);
      
      let testResult;
      if (engineKey === 'zhipu') {
        testResult = await this.translateWithZhipu('', 'en', 'zh');
      } else {
        testResult = await this.translateWithFallback('test', 'zh', 'en', engineKey);
      }
      
      const responseTime = Date.now() - startTime;
      const isHealthy = testResult && testResult.status === 'success';
      
      this.lastHealthCheck[engineKey] = {
        timestamp: Date.now(),
        healthy: isHealthy,
        responseTime,
        error: isHealthy ? null : 'Translation test failed'
      };

      engine.available = isHealthy;
      
      logger.translation(`${isHealthy ? '' : ''} [NewTranslationManager] ${engine.name} : ${responseTime}ms`);
      
      return isHealthy;
    } catch (error) {
      const responseTime = Date.now() - startTime;
      
      this.lastHealthCheck[engineKey] = {
        timestamp: Date.now(),
        healthy: false,
        responseTime,
        error: error.message
      };

      engine.available = false;
      logger.error(` [NewTranslationManager] ${engine.name} : ${error.message}`);
      
      return false;
    }
  }

  /**
   * 
   */
  startHealthCheck() {
    if (this.healthCheckInterval) {
      clearInterval(this.healthCheckInterval);
    }

    // 
    setTimeout(() => this.checkAllEnginesHealth(), 2000);

    //  (10)
    this.healthCheckInterval = setInterval(() => {
      this.checkAllEnginesHealth();
    }, 10 * 60 * 1000);

    logger.translation(' [NewTranslationManager] ');
  }

  /**
   * 
   */
  async checkAllEnginesHealth() {
    logger.translation(' [NewTranslationManager] ...');
    
    const healthPromises = Object.keys(this.engines).map(key => 
      this.checkEngineHealth(key)
    );
    
    await Promise.allSettled(healthPromises);
    
    const availableCount = Object.values(this.engines).filter(engine => engine.available).length;
    logger.translation(` [NewTranslationManager] : ${availableCount}/${Object.keys(this.engines).length}`);
    
    // 
    this.fallbackAttempts = 0;
  }

  /**
   * 
   */
  getSupportedLanguages() {
    return Object.entries(LANGUAGE_MAPPING).map(([code, info]) => ({
      code,
      name: info.name,
      nativeName: info.name
    }));
  }

  /**
   * 
   */
  getStatusReport() {
    const engines = this.getAllEngines();
    const availableEngines = engines.filter(engine => engine.available);
    const zhipuStatus = this.zhipuService.getStatus();
    
    return {
      currentEngine: this.engines[this.currentEngine]?.name || 'None',
      currentEngineKey: this.currentEngine,
      totalEngines: engines.length,
      availableEngines: availableEngines.length,
      engines: engines,
      cacheSize: this.translationCache.size,
      zhipuStatus,
      fallbackAttempts: this.fallbackAttempts,
      maxFallbackAttempts: this.maxFallbackAttempts
    };
  }

  /**
   * 
   */
  switchEngine(engineKey) {
    if (!this.engines[engineKey]) {
      throw new Error(`: ${engineKey}`);
    }
    
    const oldEngine = this.engines[this.currentEngine]?.name;
    this.currentEngine = engineKey;
    const newEngine = this.engines[this.currentEngine].name;
    
    logger.translation(` [NewTranslationManager] : ${oldEngine} -> ${newEngine}`);
    
    return this.getCurrentEngine();
  }

  /**
   * 
   */
  clearCache() {
    this.translationCache.clear();
    this.zhipuService.clearCache();
    logger.translation(' [NewTranslationManager] ');
  }

  /**
   * 
   */
  stopHealthCheck() {
    if (this.healthCheckInterval) {
      clearInterval(this.healthCheckInterval);
      this.healthCheckInterval = null;
      logger.translation(' [NewTranslationManager] ');
    }
  }
}

// 
const newTranslationManager = new NewTranslationManager();

export default newTranslationManager;
export { NewTranslationManager, LANGUAGE_MAPPING };