/**
 * GLM
 * GLM-4-Flash
 */

import { zhipuConfigManager } from '../config/zhipuConfig.js';
import { logger } from '../config/debug.js';

// 
const LANGUAGE_MAPPING = {
  'zh': '',
  'en': '',
  'ja': '',
  'ko': '',
  'fr': '',
  'de': '',
  'es': '',
  'ru': '',
  'it': '',
  'pt': '',
  'ar': '',
  'th': '',
  'vi': '',
  'hi': ''
};

/**
 * GLM
 */
class ZhipuTranslationService {
  constructor() {
    this.cache = new Map();
    this.translationCache = new Map(); // 
    this.requestQueue = [];
    this.isProcessing = false;
    this.lastRequestTime = 0;
    this.requestCount = 0;
    this.rateLimitWindow = 60000; // 1
    this.maxRequestsPerMinute = 60;
    this.config = zhipuConfigManager.getConfig(); // 
  }

  /**
   * API
   * @param {string} apiKey - AI API
   */
  setApiKey(apiKey) {
    zhipuConfigManager.setApiKey(apiKey);
    this.config = zhipuConfigManager.getConfig(); // 
    logger.translation(' [ZhipuTranslation] API');
  }

  /**
   * API
   * @returns {string|null} API
   */
  getApiKey() {
    return zhipuConfigManager.getApiKey();
  }

  /**
   * API
   * @returns {boolean} 
   */
  hasApiKey() {
    return zhipuConfigManager.hasApiKey();
  }

  /**
   * 
   */
  detectLanguage(text) {
    if (!text || typeof text !== 'string') return 'en';
    
    // 
    if (/[\u4e00-\u9fff]/.test(text)) return 'zh';
    //   
    if (/[\u3040-\u309f\u30a0-\u30ff]/.test(text)) return 'ja';
    // 
    if (/[\uac00-\ud7af]/.test(text)) return 'ko';
    // 
    if (/[\u0400-\u04ff]/.test(text)) return 'ru';
    // 
    if (/[\u0600-\u06ff]/.test(text)) return 'ar';
    
    return 'en';
  }

  /**
   * 
   */
  buildTranslationPrompt(text, targetLang, sourceLang = 'auto') {
    const sourceLanguage = sourceLang === 'auto' ? '' : (LANGUAGE_MAPPING[sourceLang] || sourceLang);
    const targetLanguage = LANGUAGE_MAPPING[targetLang] || targetLang;

    return `${sourceLanguage}${targetLanguage}


1. 
2. 
3. 
4. 
5. 


${text}

`;
  }

  /**
   * GLM API
   */
  async callZhipuAPI(prompt) {
    try {
      const config = zhipuConfigManager.getConfig();
      
      if (!config.apiKey) {
        throw new Error('AI API');
      }

      // 
      const now = Date.now();
      if (now - this.lastRequestTime < 1000) {
        await new Promise(resolve => setTimeout(resolve, 1000 - (now - this.lastRequestTime)));
      }
      this.lastRequestTime = Date.now();

      const response = await fetch(`${config.baseURL}chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${config.apiKey}`
        },
        body: JSON.stringify({
          model: config.model,
          messages: [
            {
              role: 'user',
              content: prompt
            }
          ],
          temperature: 0.1, // 
          max_tokens: 2000,
          top_p: 0.9
        })
      });

      if (!response.ok) {
        const errorData = await response.text();
        throw new Error(`API: ${response.status} ${response.statusText} - ${errorData}`);
      }

      const data = await response.json();
      
      if (data.choices && data.choices[0] && data.choices[0].message) {
        const translatedText = data.choices[0].message.content.trim();
        this.requestCount++;
        
        logger.translation(` [ZhipuTranslation]  (: ${this.requestCount})`);
        return {
          success: true,
          translatedText,
          usage: data.usage
        };
      } else {
        throw new Error('API');
      }
    } catch (error) {
      logger.error(' [ZhipuTranslation] API:', error);
      throw error;
    }
  }

  /**
   * 
   */
  async translate(text, targetLang = 'en', sourceLang = 'auto') {
    if (!text || !text.trim()) {
      throw new Error('');
    }

    if (!this.config.apiKey || this.config.apiKey === 'your-zhipu-api-key-here') {
      throw new Error('AIAPI Key: https://bigmodel.cn/usercenter/apikeys');
    }

    // 
    const cacheKey = `${text}_${sourceLang}_${targetLang}`;
    if (this.translationCache.has(cacheKey)) {
      const cached = this.translationCache.get(cacheKey);
      logger.translation(' [ZhipuTranslation] ');
      return { ...cached, fromCache: true };
    }

    try {
      // 
      const detectedLang = sourceLang === 'auto' ? this.detectLanguage(text) : sourceLang;
      
      // 
      if (detectedLang === targetLang) {
        return {
          translatedText: text,
          engine: 'zhipu-glm',
          confidence: 1.0,
          status: 'success',
          sourceLanguage: detectedLang,
          targetLanguage: targetLang,
          fromCache: false
        };
      }

      logger.translation(` [ZhipuTranslation] : "${text}" (${detectedLang}  ${targetLang})`);

      // 
      const prompt = this.buildTranslationPrompt(text, targetLang, detectedLang);

      // GLM API
      const apiResult = await this.callZhipuAPI(prompt);

      if (apiResult.success) {
        const result = {
          translatedText: apiResult.translatedText,
          engine: 'zhipu-glm',
          confidence: 0.95,
          status: 'success',
          sourceLanguage: detectedLang,
          targetLanguage: targetLang,
          fromCache: false,
          usage: apiResult.usage
        };

        // 
        this.translationCache.set(cacheKey, result);
        
        // 
        if (this.translationCache.size > 500) {
          const firstKey = this.translationCache.keys().next().value;
          this.translationCache.delete(firstKey);
        }

        return result;
      } else {
        throw new Error('');
      }
    } catch (error) {
      logger.error(' [ZhipuTranslation] :', error);
      throw new Error(`GLM: ${error.message}`);
    }
  }

  /**
   * 
   */
  async batchTranslate(texts, targetLang = 'en', sourceLang = 'auto') {
    if (!Array.isArray(texts) || texts.length === 0) {
      throw new Error('');
    }

    logger.translation(` [ZhipuTranslation]  ${texts.length} `);

    const results = [];
    for (let i = 0; i < texts.length; i++) {
      try {
        const result = await this.translate(texts[i], targetLang, sourceLang);
        results.push({
          index: i,
          original: texts[i],
          ...result
        });
        
        // 
        if (i < texts.length - 1) {
          await new Promise(resolve => setTimeout(resolve, 500));
        }
      } catch (error) {
        results.push({
          index: i,
          original: texts[i],
          error: error.message,
          status: 'failed'
        });
      }
    }

    logger.translation(` [ZhipuTranslation] : ${results.filter(r => r.status === 'success').length}/${texts.length} `);
    return results;
  }

  /**
   * 
   */
  getSupportedLanguages() {
    return Object.entries(LANGUAGE_MAPPING).map(([code, name]) => ({
      code,
      name,
      nativeName: name
    }));
  }

  /**
   * 
   */
  getStatus() {
    return {
      service: 'zhipu-glm',
      model: this.config.model,
      available: !!this.config.apiKey && this.config.apiKey !== 'your-zhipu-api-key-here',
      requestCount: this.requestCount,
      cacheSize: this.translationCache.size,
      supportedLanguages: Object.keys(LANGUAGE_MAPPING).length
    };
  }

  /**
   * 
   */
  clearCache() {
    this.translationCache.clear();
    logger.translation(' [ZhipuTranslation] ');
  }
}

// 
const zhipuTranslationService = new ZhipuTranslationService();

export default zhipuTranslationService;
export { ZhipuTranslationService, LANGUAGE_MAPPING };