import React, { useState, useEffect } from 'react';
import { Settings, Key, Globe, Palette, Database, Info, CheckCircle, AlertCircle } from 'lucide-react';
import ZhipuApiKeySettings from '../components/ZhipuApiKeySettings';
import apiManager from '../services/apiManager';

const SettingsPage = () => {
  const [activeSection, setActiveSection] = useState('api');
  const [apiStatus, setApiStatus] = useState(null);

  // API
  useEffect(() => {
    const updateApiStatus = () => {
      const status = apiManager.getStatusReport();
      setApiStatus(status);
    };

    updateApiStatus();
    
    // 30
    const interval = setInterval(updateApiStatus, 30000);
    
    return () => clearInterval(interval);
  }, []);

  const sections = [
    { id: 'api', icon: Key, label: 'API', description: 'AIAPI' },
    { id: 'general', icon: Settings, label: '', description: '' },
    { id: 'appearance', icon: Palette, label: '', description: '' },
    { id: 'data', icon: Database, label: '', description: '' },
    { id: 'about', icon: Info, label: '', description: '' }
  ];

  const handleZhipuApiKeyChange = async (hasKey) => {
    if (hasKey) {
      // APIZhipu
      await apiManager.updateZhipuApiKey();
      
      // API
      const status = apiManager.getStatusReport();
      setApiStatus(status);
    }
  };

  const renderApiSettings = () => (
    <div className="space-y-6">
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <div className="flex items-center gap-2 mb-2">
          <Info className="text-blue-600" size={20} />
          <h3 className="font-semibold text-blue-900">API</h3>
        </div>
        <p className="text-blue-800 text-sm">
          AIAPIAPI
        </p>
      </div>

      {/* API */}
      {apiStatus && (
        <div className="bg-white border border-gray-200 rounded-lg p-4">
          <h3 className="font-semibold text-gray-900 mb-3 flex items-center gap-2">
            <Globe size={20} />
            API
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-3 bg-gray-50 rounded-lg">
              <div className="text-2xl font-bold text-gray-900">{apiStatus.totalApis}</div>
              <div className="text-sm text-gray-600">API</div>
            </div>
            <div className="text-center p-3 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">{apiStatus.availableApis}</div>
              <div className="text-sm text-gray-600">API</div>
            </div>
            <div className="text-center p-3 bg-blue-50 rounded-lg">
              <div className="text-sm font-medium text-blue-900">{apiStatus.currentApi || ''}</div>
              <div className="text-sm text-gray-600">API</div>
            </div>
          </div>
        </div>
      )}

      {/* Zhipu GLM API */}
      <ZhipuApiKeySettings 
        onApiKeyChange={handleZhipuApiKeyChange}
        className="border-0 shadow-sm"
      />

      {/* API */}
      {apiStatus && apiStatus.apis && (
        <div className="bg-white border border-gray-200 rounded-lg p-4">
          <h3 className="font-semibold text-gray-900 mb-3">API</h3>
          <div className="space-y-2">
            {apiStatus.apis.map((api, index) => (
              <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  {api.available ? (
                    <CheckCircle className="text-green-500" size={16} />
                  ) : (
                    <AlertCircle className="text-red-500" size={16} />
                  )}
                  <div>
                    <div className="font-medium text-gray-900">{api.name}</div>
                    <div className="text-sm text-gray-600">{api.description || api.provider}</div>
                  </div>
                </div>
                <div className="text-right">
                  <div className={`text-sm font-medium ${api.available ? 'text-green-600' : 'text-red-600'}`}>
                    {api.available ? '' : ''}
                  </div>
                  {api.lastChecked && (
                    <div className="text-xs text-gray-500">
                      {new Date(api.lastChecked).toLocaleTimeString()}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );

  const renderGeneralSettings = () => (
    <div className="space-y-6">
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <h3 className="font-semibold text-gray-900 mb-4"></h3>
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <div>
              <div className="font-medium text-gray-900"></div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <label className="relative inline-flex items-center cursor-pointer">
              <input type="checkbox" className="sr-only peer" defaultChecked />
              <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
            </label>
          </div>
          
          <div className="flex items-center justify-between">
            <div>
              <div className="font-medium text-gray-900"></div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <label className="relative inline-flex items-center cursor-pointer">
              <input type="checkbox" className="sr-only peer" defaultChecked />
              <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
            </label>
          </div>
        </div>
      </div>
    </div>
  );

  const renderAppearanceSettings = () => (
    <div className="space-y-6">
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <h3 className="font-semibold text-gray-900 mb-4"></h3>
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <select className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent">
              <option value="light"></option>
              <option value="dark"></option>
              <option value="auto"></option>
            </select>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <select className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent">
              <option value="small"></option>
              <option value="medium"></option>
              <option value="large"></option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );

  const renderDataManagement = () => (
    <div className="space-y-6">
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <h3 className="font-semibold text-gray-900 mb-4"></h3>
        <div className="space-y-4">
          <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div>
              <div className="font-medium text-gray-900"></div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <button className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600">
              
            </button>
          </div>
          
          <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <div>
              <div className="font-medium text-gray-900"></div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <button className="px-4 py-2 bg-green-500 text-white rounded-md hover:bg-green-600">
              
            </button>
          </div>
          
          <div className="flex items-center justify-between p-3 bg-red-50 rounded-lg border border-red-200">
            <div>
              <div className="font-medium text-red-900"></div>
              <div className="text-sm text-red-600"></div>
            </div>
            <button className="px-4 py-2 bg-red-500 text-white rounded-md hover:bg-red-600">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );

  const renderAbout = () => (
    <div className="space-y-6">
      <div className="bg-white border border-gray-200 rounded-lg p-4">
        <h3 className="font-semibold text-gray-900 mb-4"> I Prompt</h3>
        <div className="space-y-4">
          <div className="text-center">
            <img 
              src={process.env.PUBLIC_URL + "/logo192.png"} 
              alt="I Prompt Logo" 
              className="w-16 h-16 mx-auto rounded-xl mb-4"
              onError={(e) => {
                e.target.style.display = 'none';
              }}
            />
            <h4 className="text-xl font-bold text-gray-900">I Prompt</h4>
            <p className="text-gray-600"></p>
            <p className="text-sm text-gray-500 mt-2"> 3.0.0</p>
          </div>
          
          <div className="border-t pt-4">
            <h5 className="font-semibold text-gray-900 mb-2"></h5>
            <ul className="text-sm text-gray-600 space-y-1">
              <li> </li>
              <li> API</li>
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
          
          <div className="border-t pt-4">
            <h5 className="font-semibold text-gray-900 mb-2"></h5>
            <p className="text-sm text-gray-600">
              
            </p>
          </div>
        </div>
      </div>
    </div>
  );

  const renderContent = () => {
    switch (activeSection) {
      case 'api':
        return renderApiSettings();
      case 'general':
        return renderGeneralSettings();
      case 'appearance':
        return renderAppearanceSettings();
      case 'data':
        return renderDataManagement();
      case 'about':
        return renderAbout();
      default:
        return renderApiSettings();
    }
  };

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2"></h1>
        <p className="text-gray-600"></p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        {/*  */}
        <div className="lg:col-span-1">
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <nav className="space-y-2">
              {sections.map((section) => {
                const IconComponent = section.icon;
                return (
                  <button
                    key={section.id}
                    onClick={() => setActiveSection(section.id)}
                    className={`w-full flex items-center gap-3 px-3 py-2 rounded-lg text-left transition-colors ${
                      activeSection === section.id
                        ? 'bg-blue-50 text-blue-700 border border-blue-200'
                        : 'text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    <IconComponent size={18} />
                    <div>
                      <div className="font-medium">{section.label}</div>
                      <div className="text-xs text-gray-500">{section.description}</div>
                    </div>
                  </button>
                );
              })}
            </nav>
          </div>
        </div>

        {/*  */}
        <div className="lg:col-span-3">
          {renderContent()}
        </div>
      </div>
    </div>
  );
};

export default SettingsPage;