/**
 * GLM
 * API
 */

// 
export const ZHIPU_CONFIG = {
  // API
  baseURL: 'https://open.bigmodel.cn/api/paas/v4/',
  model: 'glm-4-flash',
  
  // 
  maxRetries: 3,
  retryDelay: 1000,
  timeout: 30000,
  
  // 
  rateLimit: {
    requestsPerMinute: 60,
    requestsPerSecond: 2
  },
  
  // 
  cache: {
    enabled: true,
    maxSize: 1000,
    ttl: 24 * 60 * 60 * 1000 // 24
  }
};

// API
class ZhipuConfigManager {
  constructor() {
    this.apiKey = null;
    this.loadApiKey();
  }

  /**
   * localStorageAPI
   */
  loadApiKey() {
    try {
      const stored = localStorage.getItem('zhipu_api_key');
      if (stored) {
        this.apiKey = stored;
        console.log(' [ZhipuConfig] API');
      }
    } catch (error) {
      console.error(' [ZhipuConfig] API:', error);
    }
  }

  /**
   * API
   * @param {string} key - API
   */
  setApiKey(key) {
    if (!key || typeof key !== 'string') {
      throw new Error('API');
    }

    this.apiKey = key.trim();
    
    try {
      localStorage.setItem('zhipu_api_key', this.apiKey);
      console.log(' [ZhipuConfig] API');
    } catch (error) {
      console.error(' [ZhipuConfig] API:', error);
      throw new Error('API');
    }
  }

  /**
   * API
   * @returns {string|null} API
   */
  getApiKey() {
    return this.apiKey;
  }

  /**
   * API
   * @returns {boolean} 
   */
  hasApiKey() {
    return !!this.apiKey;
  }

  /**
   * API
   */
  clearApiKey() {
    this.apiKey = null;
    try {
      localStorage.removeItem('zhipu_api_key');
      console.log(' [ZhipuConfig] API');
    } catch (error) {
      console.error(' [ZhipuConfig] API:', error);
    }
  }

  /**
   * API
   * @param {string} key - API
   * @returns {boolean} 
   */
  validateApiKey(key) {
    if (!key || typeof key !== 'string') {
      return false;
    }
    
    // API
    const trimmedKey = key.trim();
    return trimmedKey.length > 10; // 
  }

  /**
   * 
   * @returns {object} 
   */
  getConfig() {
    return {
      ...ZHIPU_CONFIG,
      apiKey: this.apiKey
    };
  }
}

// 
export const zhipuConfigManager = new ZhipuConfigManager();

// 
export const setZhipuApiKey = (key) => zhipuConfigManager.setApiKey(key);
export const getZhipuApiKey = () => zhipuConfigManager.getApiKey();
export const hasZhipuApiKey = () => zhipuConfigManager.hasApiKey();
export const clearZhipuApiKey = () => zhipuConfigManager.clearApiKey();
export const validateZhipuApiKey = (key) => zhipuConfigManager.validateApiKey(key);