import { API_CONFIG } from '../constants/config.js';
import { zhipuConfigManager } from '../config/zhipuConfig.js';
import { logger } from '../config/debug.js';

/**
 * API
 * API
 */
class ApiManager {
  constructor() {
    this.apis = [...API_CONFIG.APIS];
    this.currentApi = null;
    this.healthCheckInterval = null;
    this.lastHealthCheck = {};
  }

  /**
   * API
   */
  async init() {
    logger.api(' [ApiManager] API...');
    
    // API
    this.apis = JSON.parse(JSON.stringify(API_CONFIG.APIS));
    
    // GLMAPI
    const zhipuApi = this.apis.find(api => api.provider === 'zhipu');
    if (zhipuApi) {
      const zhipuConfig = zhipuConfigManager.getConfig();
      if (zhipuConfig.apiKey) {
        zhipuApi.apiKey = zhipuConfig.apiKey;
        logger.api(' [ApiManager] GLMAPI');
      } else {
        logger.warn(' [ApiManager] GLMAPI');
      }
    }
    
    // 
    this.lastHealthCheck = {};
    
    // 
    await this.checkAllApisHealth();
    
    // API
    await this.switchToNextAvailableApi();
    
    logger.api(` [ApiManager] API: ${this.currentApi?.name || 'None'}`);
  }



  /**
   * API
   */
  getCurrentApi() {
    return this.currentApi;
  }

  /**
   * API
   */
  getAllApis() {
    return this.apis.map(api => ({
      name: api.name,
      provider: api.provider,
      available: api.available,
      priority: api.priority,
      lastCheck: this.lastHealthCheck[api.name]
    }));
  }

  /**
   * API
   */
  async checkApiHealth(api) {
    const startTime = Date.now();
    
    try {
      logger.api(` [ApiManager] API: ${api.name}`);
      
      // GLMAPI
      if (api.provider === 'zhipu' && api.requiresApiKey && !api.apiKey) {
        logger.warn(` [ApiManager] ${api.name} API`);
        api.available = false;
        this.lastHealthCheck[api.name] = {
          timestamp: Date.now(),
          healthy: false,
          responseTime: 0,
          status: null,
          error: 'API'
        };
        return false;
      }
      
      // 
      const testRequest = {
        model: api.model,
        messages: [
          {
            role: 'user',
            content: 'Test connection'
          }
        ],
        max_tokens: 10,
        temperature: 0.7
      };

      // 
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${api.apiKey}`,
        ...api.headers // API
      };

      // API
      let endpoint;
      if (api.provider === 'zhipu') {
        endpoint = `${api.baseUrl}chat/completions`;
      } else {
        endpoint = `${api.baseUrl}/chat/completions`;
      }

      const response = await fetch(endpoint, {
        method: 'POST',
        headers,
        body: JSON.stringify(testRequest),
        signal: AbortSignal.timeout(API_CONFIG.HEALTH_CHECK.TIMEOUT)
      });

      const responseTime = Date.now() - startTime;
      const isHealthy = response.ok || response.status === 400; // 400API

      this.lastHealthCheck[api.name] = {
        timestamp: Date.now(),
        healthy: isHealthy,
        responseTime,
        status: response.status,
        error: isHealthy ? null : `HTTP ${response.status}`
      };

      // API
      api.available = isHealthy;

      logger.api(`${isHealthy ? '' : ''} [ApiManager] ${api.name} : ${responseTime}ms, : ${response.status}`);

      return isHealthy;

    } catch (error) {
      const responseTime = Date.now() - startTime;
      
      this.lastHealthCheck[api.name] = {
        timestamp: Date.now(),
        healthy: false,
        responseTime,
        status: null,
        error: error.message || 'Network error'
      };

      api.available = false;
      
      logger.error(` [ApiManager] ${api.name} : ${error.message}`);
      
      return false;
    }
  }

  /**
   * API
   */
  async checkAllApisHealth() {
    logger.api(' [ApiManager] ...');
    
    const healthPromises = this.apis.map(api => this.checkApiHealth(api));
    await Promise.allSettled(healthPromises);
    
    // API
    if (!this.currentApi?.available) {
      await this.switchToNextAvailableApi();
    }
    
    const availableCount = this.apis.filter(api => api.available).length;
    logger.api(` [ApiManager] API: ${availableCount}/${this.apis.length}`);
  }

  /**
   * API
   */
  async switchToNextAvailableApi() {
    const availableApis = this.apis.filter(api => api.available);
    
    if (availableApis.length === 0) {
      logger.warn(' [ApiManager] API');
      return false;
    }

    // API
    const nextApi = availableApis[0];
    
    if (nextApi !== this.currentApi) {
      const oldApi = this.currentApi?.name || 'None';
      this.currentApi = nextApi;
      logger.api(` [ApiManager] API: ${oldApi} -> ${nextApi.name}`);
      
      // 
      this.onApiSwitch?.(nextApi, oldApi);
    }

    return true;
  }

  /**
   * API
   */
  async makeRequest(messages, options = {}) {
    if (!this.currentApi) {
      throw new Error('API');
    }

    const api = this.currentApi;
    
    // GLMAPI
    if (api.provider === 'zhipu' && api.requiresApiKey && !api.apiKey) {
      throw new Error('GLMAPI');
    }
    
    const requestBody = {
      model: api.model,
      messages: messages,
      ...API_CONFIG.DEFAULT_PARAMS,
      ...options
    };

    logger.api(` [ApiManager]  ${api.name} `);
    logger.api(` [ApiManager] :`, JSON.stringify(requestBody, null, 2));

    try {
      // 
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${api.apiKey}`,
        ...api.headers // API
      };

      logger.api(` [ApiManager] :`, headers);

      // API
      let endpoint;
      if (api.provider === 'zhipu') {
        endpoint = `${api.baseUrl}chat/completions`;
      } else {
        endpoint = `${api.baseUrl}/chat/completions`;
      }

      const response = await fetch(endpoint, {
        method: 'POST',
        headers,
        body: JSON.stringify(requestBody),
        signal: AbortSignal.timeout(30000) // 30
      });

      logger.api(` [ApiManager] : ${response.status} ${response.statusText}`);

      if (!response.ok) {
        // 
        let errorText = '';
        try {
          errorText = await response.text();
          logger.error(` [ApiManager] :`, errorText);
        } catch (e) {
          logger.error(` [ApiManager] `);
        }

        // API
        api.available = false;
        this.lastHealthCheck[api.name] = {
          timestamp: Date.now(),
          healthy: false,
          responseTime: 0,
          status: response.status,
          error: `HTTP ${response.status}: ${errorText}`
        };

        // API
        const switched = await this.switchToNextAvailableApi();
        if (switched && this.currentApi !== api) {
          logger.api(` [ApiManager]  ${this.currentApi.name}`);
          return this.makeRequest(messages, options); // 
        }

        throw new Error(`API: ${response.status} ${response.statusText}${errorText ? ': ' + errorText : ''}`);
      }

      const data = await response.json();
      logger.api(` [ApiManager] :`, JSON.stringify(data, null, 2));
      
      // 
      if (!data.choices || !data.choices[0] || !data.choices[0].message) {
        logger.warn(` [ApiManager] ${api.name} `);
        throw new Error('API');
      }

      const content = data.choices[0].message.content;
      if (!content || content.trim().length === 0) {
        logger.warn(` [ApiManager] ${api.name} `);
        throw new Error('API');
      }

      logger.api(` [ApiManager] ${api.name} : ${content.length}`);
      
      return data;

    } catch (error) {
      logger.error(` [ApiManager] ${api.name} :`, error);
      
      // API
      api.available = false;
      
      // API
      const switched = await this.switchToNextAvailableApi();
      if (switched && this.currentApi !== api) {
        logger.api(` [ApiManager]  ${this.currentApi.name}`);
        return this.makeRequest(messages, options); // 
      }

      throw error;
    }
  }

  /**
   * 
   */
  startHealthCheck() {
    if (this.healthCheckInterval) {
      clearInterval(this.healthCheckInterval);
    }

    // 
    this.checkAllApisHealth();

    // 
    this.healthCheckInterval = setInterval(() => {
      this.checkAllApisHealth();
    }, API_CONFIG.HEALTH_CHECK.RETRY_INTERVAL);

    logger.api(' [ApiManager] ');
  }

  /**
   * 
   */
  stopHealthCheck() {
    if (this.healthCheckInterval) {
      clearInterval(this.healthCheckInterval);
      this.healthCheckInterval = null;
      logger.api(' [ApiManager] ');
    }
  }

  /**
   * API
   */
  async refreshApis() {
    logger.api(' [ApiManager] API...');
    await this.checkAllApisHealth();
  }

  /**
   * API
   */
  onApiSwitch(callback) {
    this.onApiSwitch = callback;
  }

  /**
   * API
   */
  getStatusReport() {
    const report = {
      currentApi: this.currentApi?.name || 'None',
      totalApis: this.apis.length,
      availableApis: this.apis.filter(api => api.available).length,
      apis: this.getAllApis()
    };

    return report;
  }

  /**
   * API
   */
  setPreferredApi(targetApi) {
    const foundApi = this.apis.find(api => api.name === targetApi.name);
    if (foundApi) {
      const oldApi = this.currentApi?.name || 'None';
      this.currentApi = foundApi;
      logger.api(` [ApiManager] API: ${oldApi} -> ${foundApi.name}`);
      
      // 
      this.onApiSwitch?.(foundApi, oldApi);
      return true;
    }
    return false;
  }

  /**
   * GLM API
   */
  async updateZhipuApiKey() {
    const zhipuApi = this.apis.find(api => api.provider === 'zhipu');
    if (zhipuApi) {
      const zhipuConfig = zhipuConfigManager.getConfig();
      if (zhipuConfig.apiKey) {
        zhipuApi.apiKey = zhipuConfig.apiKey;
        logger.api(' [ApiManager] GLM API');
        
        // 
        await this.checkApiHealth(zhipuApi);
        
        // API
        if (!this.currentApi?.available) {
          await this.switchToNextAvailableApi();
        }
        
        return true;
      } else {
        zhipuApi.apiKey = null;
        zhipuApi.available = false;
        logger.warn(' [ApiManager] GLM API');
        return false;
      }
    }
    return false;
  }
}

// 
const apiManager = new ApiManager();

export default apiManager;