// 
const isProduction = process.env.NODE_ENV === 'production';

// 
export const DEBUG_CONFIG = {
  // 
  ENABLE_LOGS: !isProduction,
  
  // 
  TRANSLATION_LOGS: !isProduction,
  
  // API
  API_LOGS: !isProduction,
  
  // 
  COMPONENT_LOGS: !isProduction,
  
  //  ()
  ERROR_LOGS: true,
  
  //  ()
  WARN_LOGS: true
};

/**
 * 
 */
export const logger = {
  log: (...args) => {
    if (DEBUG_CONFIG.ENABLE_LOGS) {
      console.log(...args);
    }
  },
  
  warn: (...args) => {
    if (DEBUG_CONFIG.WARN_LOGS) {
      console.warn(...args);
    }
  },
  
  error: (...args) => {
    if (DEBUG_CONFIG.ERROR_LOGS) {
      console.error(...args);
    }
  },
  
  translation: (...args) => {
    if (DEBUG_CONFIG.TRANSLATION_LOGS) {
      console.log(...args);
    }
  },
  
  api: (...args) => {
    if (DEBUG_CONFIG.API_LOGS) {
      console.log(...args);
    }
  },
  
  component: (...args) => {
    if (DEBUG_CONFIG.COMPONENT_LOGS) {
      console.log(...args);
    }
  },
  
  ui: (...args) => {
    if (DEBUG_CONFIG.COMPONENT_LOGS) {
      console.log(...args);
    }
  },
  
  analysis: (...args) => {
     if (DEBUG_CONFIG.ENABLE_LOGS) {
       console.log(...args);
     }
   }
 };

export default logger;