import React, { useState } from 'react';
import { X, ChevronLeft, ChevronRight, BookOpen, Sparkles, Edit3, Heart } from 'lucide-react';

/**
 *  - 
 */
const TutorialModal = ({ onClose }) => {
  const [currentStep, setCurrentStep] = useState(0);

  const tutorialSteps = [
    {
      title: "AI",
      icon: <Sparkles className="text-purple-600" size={32} />,
      content: (
        <div className="space-y-4">
          <p className="text-gray-700">
            AIAI
          </p>
          <div className="bg-purple-50 p-4 rounded-lg">
            <h4 className="font-semibold text-purple-900 mb-2"></h4>
            <ul className="text-purple-800 space-y-1 text-sm">
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
        </div>
      )
    },
    {
      title: "",
      icon: <Edit3 className="text-blue-600" size={32} />,
      content: (
        <div className="space-y-4">
          <p className="text-gray-700">
            
          </p>
          <div className="bg-blue-50 p-4 rounded-lg">
            <h4 className="font-semibold text-blue-900 mb-2"></h4>
            <ul className="text-blue-800 space-y-1 text-sm">
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong>  </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
            </ul>
          </div>
          <div className="bg-yellow-50 p-3 rounded-lg border border-yellow-200">
            <p className="text-yellow-800 text-sm">
               <strong></strong> ", "
            </p>
          </div>
        </div>
      )
    },
    {
      title: "",
      icon: <BookOpen className="text-green-600" size={32} />,
      content: (
        <div className="space-y-4">
          <p className="text-gray-700">
            
          </p>
          <div className="bg-green-50 p-4 rounded-lg">
            <h4 className="font-semibold text-green-900 mb-2"></h4>
            <ul className="text-green-800 space-y-1 text-sm">
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
            </ul>
          </div>
          <div className="bg-gray-50 p-3 rounded-lg">
            <p className="text-gray-700 text-sm">
               <strong></strong> 
            </p>
          </div>
        </div>
      )
    },
    {
      title: "",
      icon: <Heart className="text-red-600" size={32} />,
      content: (
        <div className="space-y-4">
          <p className="text-gray-700">
            
          </p>
          <div className="bg-red-50 p-4 rounded-lg">
            <h4 className="font-semibold text-red-900 mb-2"></h4>
            <ul className="text-red-800 space-y-1 text-sm">
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
              <li> <strong></strong> </li>
            </ul>
          </div>
          <div className="grid grid-cols-2 gap-3 mt-4">
            <div className="bg-white p-3 border border-red-200 rounded-lg">
              <div className="text-red-600 font-medium text-sm mb-1"></div>
              <div className="text-xs text-gray-600"></div>
            </div>
            <div className="bg-white p-3 border border-red-200 rounded-lg">
              <div className="text-red-600 font-medium text-sm mb-1"></div>
              <div className="text-xs text-gray-600"></div>
            </div>
          </div>
        </div>
      )
    },
    {
      title: "",
      icon: <Sparkles className="text-indigo-600" size={32} />,
      content: (
        <div className="space-y-4">
          <p className="text-gray-700">
            AI
          </p>
          <div className="space-y-3">
            <div className="bg-indigo-50 p-4 rounded-lg border border-indigo-200">
              <h4 className="font-semibold text-indigo-900 mb-2"></h4>
              <ul className="text-indigo-800 space-y-1 text-sm">
                <li> <code className="bg-white px-1 rounded">tag:1.2</code> - 20%</li>
                <li> <code className="bg-white px-1 rounded">tag:0.8</code> - 20%</li>
                <li> <code className="bg-white px-1 rounded">(tag)</code> - </li>
                <li> <code className="bg-white px-1 rounded">((tag))</code> - </li>
                <li> <code className="bg-white px-1 rounded">[tag]</code> - </li>
              </ul>
            </div>
            
            <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200">
              <h4 className="font-semibold text-yellow-900 mb-2"></h4>
              <ul className="text-yellow-800 space-y-1 text-sm">
                <li> </li>
                <li> </li>
                <li> </li>
                <li> </li>
              </ul>
            </div>
          </div>
        </div>
      )
    }
  ];

  const nextStep = () => {
    setCurrentStep((prev) => Math.min(prev + 1, tutorialSteps.length - 1));
  };

  const prevStep = () => {
    setCurrentStep((prev) => Math.max(prev - 1, 0));
  };

  const currentTutorial = tutorialSteps[currentStep];

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-2xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-hidden">
        {/*  */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <div className="flex items-center gap-3">
            {currentTutorial.icon}
            <h2 className="text-xl font-bold text-gray-900">
              {currentTutorial.title}
            </h2>
          </div>
          <button
            onClick={onClose}
            className="p-2 text-gray-500 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <X size={20} />
          </button>
        </div>

        {/*  */}
        <div className="px-6 py-4 bg-gray-50">
          <div className="flex items-center justify-between mb-2">
            <span className="text-sm text-gray-600">
               {currentStep + 1} / {tutorialSteps.length}
            </span>
            <span className="text-sm text-gray-600">
              {Math.round(((currentStep + 1) / tutorialSteps.length) * 100)}%
            </span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-2">
            <div 
              className="bg-blue-600 h-2 rounded-full transition-all duration-300"
              style={{ width: `${((currentStep + 1) / tutorialSteps.length) * 100}%` }}
            />
          </div>
        </div>

        {/*  */}
        <div className="p-6 overflow-y-auto max-h-96">
          {currentTutorial.content}
        </div>

        {/*  */}
        <div className="flex items-center justify-between p-6 border-t border-gray-200 bg-gray-50">
          <button
            onClick={prevStep}
            disabled={currentStep === 0}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
              currentStep === 0
                ? 'text-gray-400 cursor-not-allowed'
                : 'text-gray-700 hover:bg-gray-200'
            }`}
          >
            <ChevronLeft size={20} />
            
          </button>

          <div className="flex gap-2">
            {tutorialSteps.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentStep(index)}
                className={`w-3 h-3 rounded-full transition-colors ${
                  index === currentStep 
                    ? 'bg-blue-600' 
                    : index < currentStep 
                      ? 'bg-blue-300' 
                      : 'bg-gray-300'
                }`}
              />
            ))}
          </div>

          {currentStep < tutorialSteps.length - 1 ? (
            <button
              onClick={nextStep}
              className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              
              <ChevronRight size={20} />
            </button>
          ) : (
            <button
              onClick={onClose}
              className="flex items-center gap-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
            >
              
              <Sparkles size={20} />
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default TutorialModal; 