import { useState, useCallback, useEffect } from 'react';
import translationManager from '../services/newTranslationManager';

/**
 * Hook
 * 
 */
export const useMultiTranslation = () => {
  const [isTranslating, setIsTranslating] = useState(false);
  const [currentEngine, setCurrentEngine] = useState(null);
  const [translationError, setTranslationError] = useState(null);
  const [translationHistory, setTranslationHistory] = useState([]);

  // 
  useEffect(() => {
    // 
    translationManager.onEngineSwitch = (newEngine, oldEngine) => {
      setCurrentEngine(newEngine);
      console.log(` [Hook] : ${oldEngine} -> ${newEngine.name}`);
    };

    // 
    setCurrentEngine(translationManager.getCurrentEngine());
  }, []);

  /**
   * 
   */
  const translate = useCallback(async (text, targetLang = 'en', sourceLang = 'auto') => {
    if (!text || !text.trim()) {
      setTranslationError('');
      return null;
    }

    setIsTranslating(true);
    setTranslationError(null);

    try {
      const result = await translationManager.smartTranslate(text, targetLang, sourceLang);
      
      // 
      const historyItem = {
        id: Date.now(),
        originalText: text,
        translatedText: result.translatedText,
        sourceLang,
        targetLang,
        engine: result.engine,
        confidence: result.confidence,
        timestamp: new Date(),
        fromCache: result.fromCache || false
      };

      setTranslationHistory(prev => [historyItem, ...prev.slice(0, 49)]); // 50

      return result;
    } catch (error) {
      console.error(':', error);
      setTranslationError(error.message || '');
      return null;
    } finally {
      setIsTranslating(false);
    }
  }, []);

  /**
   * 
   */
  const batchTranslate = useCallback(async (texts, targetLang = 'en', sourceLang = 'auto') => {
    if (!texts || !Array.isArray(texts) || texts.length === 0) {
      setTranslationError('');
      return [];
    }

    setIsTranslating(true);
    setTranslationError(null);

    try {
      const results = [];
      for (let i = 0; i < texts.length; i++) {
        const text = texts[i];
        if (text && text.trim()) {
          try {
            const result = await translationManager.smartTranslate(text, targetLang, sourceLang);
            results.push({
              index: i,
              original: text,
              translated: result.translatedText,
              engine: result.engine,
              confidence: result.confidence,
              success: true
            });
          } catch (error) {
            results.push({
              index: i,
              original: text,
              translated: null,
              error: error.message,
              success: false
            });
          }
        } else {
          results.push({
            index: i,
            original: text,
            translated: null,
            error: '',
            success: false
          });
        }

        // 
        if (i < texts.length - 1) {
          await new Promise(resolve => setTimeout(resolve, 200));
        }
      }

      return results;
    } catch (error) {
      console.error(':', error);
      setTranslationError(error.message || '');
      return [];
    } finally {
      setIsTranslating(false);
    }
  }, []);

  /**
   * 
   */
  const getEngineStatus = useCallback(() => {
    return translationManager.getStatusReport();
  }, []);

  /**
   * 
   */
  const switchEngine = useCallback((engineKey) => {
    try {
      const newEngine = translationManager.switchEngine(engineKey);
      setCurrentEngine(newEngine);
      setTranslationError(null);
      return newEngine;
    } catch (error) {
      setTranslationError(error.message);
      return null;
    }
  }, []);

  /**
   * 
   */
  const getAllEngines = useCallback(() => {
    return translationManager.getAllEngines();
  }, []);

  /**
   * 
   */
  const detectLanguage = useCallback((text) => {
    return translationManager.detectLanguage(text);
  }, []);

  /**
   * 
   */
  const clearCache = useCallback(() => {
    translationManager.clearCache();
    setTranslationHistory([]);
  }, []);

  /**
   * 
   */
  const refreshEngines = useCallback(async () => {
    try {
      await translationManager.checkAllEnginesHealth();
      setCurrentEngine(translationManager.getCurrentEngine());
    } catch (error) {
      console.error(':', error);
    }
  }, []);

  /**
   * 
   */
  const getTranslationStats = useCallback(() => {
    const stats = {
      totalTranslations: translationHistory.length,
      engineUsage: {},
      languagePairs: {},
      cacheHitRate: 0
    };

    let cacheHits = 0;

    translationHistory.forEach(item => {
      // 
      stats.engineUsage[item.engine] = (stats.engineUsage[item.engine] || 0) + 1;

      // 
      const langPair = `${item.sourceLang} -> ${item.targetLang}`;
      stats.languagePairs[langPair] = (stats.languagePairs[langPair] || 0) + 1;

      // 
      if (item.fromCache) {
        cacheHits++;
      }
    });

    stats.cacheHitRate = translationHistory.length > 0 
      ? Math.round((cacheHits / translationHistory.length) * 100) 
      : 0;

    return stats;
  }, [translationHistory]);

  return {
    // 
    isTranslating,
    currentEngine,
    translationError,
    translationHistory,

    // 
    translate,
    batchTranslate,

    // 
    switchEngine,
    getAllEngines,
    getEngineStatus,
    refreshEngines,

    // 
    detectLanguage,
    clearCache,
    getTranslationStats
  };
};